.cards {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(310px, 1fr));
}

.card-eth-wallet {

    white-space: nowrap;
    overflow: hidden;
    font-size: 14px;
    text-align: left;
    text-overflow: ellipsis;
}

.card-padding {
    padding: 5px 10px;
}

.card-text-right {
    text-align: right;
}

.card {
    background: #FFFFFF;
    max-width: 400px;
    margin: 5px auto;
    border-radius: 12px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
    box-sizing: border-box;
    text-align: center;
}

.grey-text.text-darken-4 {
    color: #FFFFFF !important;
}

.card-value-margin-btm {
    margin-bottom: 5px;
}